'use strict';

// MODULES //

var proc = require( 'process' );


// MAIN //

/**
* An object containing the user environment.
*
* @name ENV
* @type {Object}
*
* @example
* console.dir( ENV );
*/
var ENV = proc.env;


// EXPORTS //

module.exports = ENV;
